import React,{Component} from "react";
export default class Task extends Component{
    constructor(){
        super();
        this.state={
            task:false,
            offline:false,
            backgroundColor:"orange",
            color:"gray",
            time:"",
            objCon:{
                content:"",
                btnCont:"",
                stateCont:""
            },
        }
    }
    render(){
        let {task,objCon,time,color,backgroundColor,offline} = this.state;
       return(   
        <div className="task-con">
        <span style={{display:task?"none":"block"}}>暂无计划任务</span>
        <div className="task" style={{display:task?"block":"none"}}>
            <div >调峰任务</div>
            <div className="task-center">
                <div >{objCon.content}</div>
                <button style={{background:backgroundColor,border:"none",color:"#fff",borderRadius:".05rem",textDecoration:offline?"line-through":"none"}}>
                    {objCon.btnCont} {time}
                </button>
            </div>
            <div style={{color:color}}>{objCon.stateCont}</div>
        </div>
        {/* 已打断（您可能用遥控器等方式修改了运行参数）       未参与（任务开始时未检测到在线设备） */}
    </div>
       )
    }
    componentDidMount(){
        var startTime = new Date("2019/4/10 19:07");
        var endTime = new Date("2019/4/10 19:30");   
        let nowTime = new Date();
        if(nowTime<startTime){
            this.setState({
                task:true,
                objCon:{
                    content:"今晚30分钟",
                    btnCont:"距离开始还剩",
                    stateCont:"4月10日18:20~18:30"
                },
                backgroundColor:"orange",
                color:"gray",
            })
            this.countDown(startTime,nowTime,endTime)  
        }else if(startTime<=nowTime&&nowTime<=endTime){
            this.setState({
                task:true,
                objCon:{
                    content:"18:20~18:30",
                    btnCont:"还剩",
                    stateCont:"参与中，请不要手动调节家电运行参数"
                },
                backgroundColor:"#00cc66",
                color:"#00cc66",
            })
            this.countDown(endTime,nowTime)  
        }
    }
    countDown = (startTime,nowTime,endTime) => {
        var remaining = startTime  - nowTime;
        let hour = Math.floor((remaining / 1000 / 3600) % 24);
        let minute = Math.floor((remaining / 1000 / 60) % 60);
        let second = Math.floor(remaining / 1000 % 60);
        hour = hour < 10 ? "0" + hour : hour;
        minute = minute < 10 ? "0" + minute : minute;
        second = second < 10 ? "0" + second : second;
        this.setState({
            time:hour+":"+minute+":"+second
        })
        this.timer = setInterval(() => {
        //防止出现负数
         if (remaining > 1000) {
          remaining -= 1000;
        //    let day = Math.floor((remaining / 1000 / 3600) / 24);
           let hour = Math.floor((remaining / 1000 / 3600) % 24);
           let minute = Math.floor((remaining / 1000 / 60) % 60);
           let second = Math.floor(remaining / 1000 % 60);
            hour = hour < 10 ? "0" + hour : hour;
            minute = minute < 10 ? "0" + minute : minute;
            second = second < 10 ? "0" + second : second;
           this.setState({
                 time:hour+":"+minute+":"+second
             })
          } else {
            clearInterval(this.timer);
         //任务开始倒计时结束时触发进行时
            let now = new Date();
            if(now<endTime){
                this.setState({
                    objCon:{
                        content:"18:20~18:30",
                        btnCont:"还剩",
                        stateCont:"参与中，请不要手动调节家电运行参数"
                    },
                    backgroundColor:"#00cc66",
                    color:"#00cc66",
                })
                this.countDown(endTime,now)  
            }else{
                this.setState({
                    task:false
                })
            }
            
         }
        }, 1000);
       }
}